<template>
  <div class="empty">
    <!--  -->
    <van-nav-bar
      fixed
      title=""
      left-text=""
      right-text=""
      left-arrow
      @click-left="onClickLeft"
      @click-right="onClickRight"
    />
    <div class="tree">
        <vueGiantTree :nodes="trees" @onClick="nodeClick" :setting="setting" ref="tree"></vueGiantTree>
    </div>
  </div>
</template>
<script>
import vueGiantTree from "vue-giant-tree";
import banck from "@/api/banch";
export default {
  components: { vueGiantTree },
  props: ["id"],
  data() {
    return {
      trees: [],
      nodes: [],
      setting: {
        data: {
          key: {
            name: "hdName"
          }
        },
        check: {
          enable: true,
          chkStyle: "checkbox",
          chkboxType: { Y: "", N: "" },
          chkDisabledInherit: true
        },
        view: {
          showIcon: false
        }
      }
    };
  },
  created() {
    this.getInit();
  },
  methods: {
    getInit() {
      banck.getEmpty(this.id).then(res => {
        if (res.data.success == 0) {
          this.trees = res.data.rows;
        }
      });
    },
    nodeClick(evt, treeId, treeNode) {
     if (treeNode.children.length !==0) {
        this.$toast("");
        return false;
      }
      // 
      this.$refs.tree.ztreeObj.checkNode(treeNode, !treeNode.checked, false);
      // 
      let _nodes = this.$refs.tree.ztreeObj.getCheckedNodes(true);
      this.nodes = _nodes;
    },
    onClickRight() {
      this.$emit("getEmpty", this.nodes);
      this.$emit("cancel");
    },
    onClickLeft(){
       this.$emit("cancel");
    }
  }
};
</script>
<style scoped>
.tree{
    padding: 10px;
    margin-top:1rem;
}
</style>


